*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #000000;
}

ul {
  list-style: none;
  text-transform: capitalize;
}
ul li {
  line-height: 1.9em;
}

h1,
h2,
h3,
h4 {
  color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

p {
  margin: 0.5rem 0;
}

img {
  width: 100%;
}

/* showcase styling */
.showcase {
  height: 93vh;
  width: 100%;
  position: relative;
  background: url("../images/pogo-tilt-2.jpg") no-repeat center center/cover;
}
@media only screen and (max-width: 959px) {
  .showcase {
    height: 70vh;
  }
}

.showcase::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(0, 0, 0, 0.65);
  box-shadow: inset 120px 100px 250px #000000, inset -120px -100px 250px #000000;
}
@media only screen and (max-width: 699px) {
  .showcase::after {
    box-shadow: inset 80px 80px 250px #000000, inset -80px -80px 250px #000000;
  }
}

.showcase-top {
  height: 90px;
  position: relative;
  z-index: 2;
}
.showcase-top img {
  width: 170px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media only screen and (max-width: 959px) {
  .showcase-top img {
    top: 30%;
    left: 5%;
    transform: translate(0);
  }
}
.showcase-top .btn {
  position: absolute;
  top: 50%;
  right: 0%;
  transform: translate(-50%, -50%);
}

.showcase-content {
  position: relative;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 9rem;
  z-index: 2;
}

/* Tabs styling */
.tabs {
  background-color: #141414;
  color: #868c91;
  padding-top: 1rem;
  border-bottom: 3px solid #3d3d3d;
}

.tabs .container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.tabs .container > div {
  padding: 1rem;
}

.tabs .container > div:hover {
  color: #ffffff;
  cursor: pointer;
}

.tab-border {
  border-bottom: 4px solid #e50914;
}

/* Tab Content */
.tab-content {
  padding: 3rem 0;
  background: #000000;
  color: #ffffff;
}

#tab-1-content .tab-content-1-inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 699px) {
  #tab-1-content .tab-content-1-inner {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

#tab-2-content .tab-content-2-top {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-gap: 1rem;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 699px) {
  #tab-2-content .tab-content-2-top {
    display: block;
    text-align: center;
  }
}
#tab-2-content .tab-content-2-bottom {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media only screen and (max-width: 699px) {
  #tab-2-content .tab-content-2-bottom {
    grid-template-columns: 1fr;
  }
}

.table {
  width: 100%;
  margin-top: 2rem;
  border-collapse: collapse;
  border-spacing: 0;
}
.table thead th {
  text-transform: uppercase;
  padding: 0.8rem;
}
.table tbody tr td {
  color: #999999;
  padding: 0.8rem 1.2rem;
  text-align: center;
}
.table tbody tr td i {
  font-size: 1.2rem;
}
.table tbody tr td:first-child {
  text-align: left;
}
.table tbody tr:nth-child(odd) {
  background: #222;
}

#tab-1-content,
#tab-2-content,
#tab-3-content {
  display: none;
}

.show {
  display: block !important;
}

/* Footer */
.footer {
  width: 75%;
  margin: 1rem auto;
  overflow: auto;
}

.footer .footer-cols {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2em;
}
@media only screen and (max-width: 959px) {
  .footer .footer-cols {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Global butttons */
.btn {
  display: inline-block;
  background-color: #e50914;
  color: #ffffff;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  text-align: center;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  outline: none;
  margin-right: 0.5rem;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.45);
}

.btn:hover {
  opacity: 0.9;
}

.btn-rounded {
  border-radius: 5px;
}

.btn-lg {
  font-size: 1rem;
  padding: 0.8rem 1.3rem;
  text-transform: uppercase;
}

.btn-xl {
  font-size: 2rem;
  padding: 1.5rem 2.1rem;
  text-transform: uppercase;
}
@media only screen and (max-width: 959px) {
  .btn-xl {
    font-size: 1.5rem;
    padding: 1.4rem 2rem;
  }
}

/* Global container */
.container {
  width: 70%;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
}

body {
  color: #868c91;
  font-family: Arial, Helvetica, sans-serif;
}

.showcase-content h1 {
  font-size: 5.2rem;
  font-weight: 700;
  line-height: 1.1;
  margin: 0 0 2rem;
}
@media only screen and (max-width: 959px) {
  .showcase-content h1 {
    font-size: 3.7rem;
    line-height: 1;
  }
}
.showcase-content p {
  font-size: 1.9rem;
  color: #ffffff;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  line-height: 1.25;
  margin: 0 0 2rem;
}
@media only screen and (max-width: 959px) {
  .showcase-content p {
    font-size: 1.5rem;
  }
}

.tabs p {
  font-size: 1.2rem;
  padding-top: 1.5rem;
}

.tab-item .fas {
  font-size: 5em;
}
@media only screen and (max-width: 959px) {
  .tab-item .fas {
    font-size: 3em;
  }
}

.footer {
  color: #999999;
}
.footer a {
  color: #999999;
  font-size: 0.9rem;
}
.footer p {
  margin-bottom: 1.5rem;
}

.text-lg {
  color: #ffffff;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 959px) {
  .text-lg {
    font-size: 1.3rem;
  }
}

.text-xl {
  color: #ffffff;
  font-size: 2rem;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 959px) {
  .text-xl {
    font-size: 1.5rem;
  }
}

.text-center {
  text-align: center;
  color: #ffffff;
}

.text-dark {
  color: #999999;
}

.text-md {
  font-size: 1.3rem;
  color: #ffffff;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 959px) {
  .text-md {
    font-size: 1rem;
  }
}

.text-cap {
  text-transform: uppercase;
}

@media only screen and (max-width: 959px) {
  .hide-sm {
    display: none;
  }
}